.nav {
  &__item {
    position: relative;

    display: inline-block;
    margin-left: 20px;
    line-height: $header-height;
  }

  &__link {
    color: #000;
    font-weight: 500;
    text-decoration: none;
    text-transform: uppercase;

    &:hover {
      color: #00acdc;
    }
  }

  &__link--is-active {
    color: #00acdc;

    &::after {
      content: "";
      position: absolute;
      left: 0;
      bottom: 0;
    
      width: 37px;
      height: 4px;
      border-radius: 8px;
    
      background-color: #00acdc;
    }
  }
}
